/* ==================== */
/* == Paths
/* ==================== */

$path: "../" !default;


/* ==================== */
/* == Colors
/* ==================== */

$colors: (
  dominant: (
    base: #444,
    light: mediumblue,
    dark: black,
    contrast: white
  ),
  secondary: (
    base: mediumblue,
    light: deepskyblue,
    dark: DarkBlue,
    contrast: white
  ),
  accent: (
    base: rebeccapurple,
    light: blueviolet,
    dark: indigo,
    contrast: white
  ),
  muted: (
    base: gray,
    light: darkgray,
    contrast: black
  ),
  alert: (
    base: crimson,
    dark: firebrick,
    contrast: white
  ),
  success: (
    base: springgreen,
    light: palegreen,
    dark: mediumseagreen,
    contrast: #444
  ),
  default: (
    base: dimgray,
    secondary: whitesmoke,
    contrast: white
  )
);

$blank: str-replace(to-string(palette( accent )), "#", "%23");
$blank-focus: str-replace(to-string(palette( accent, dark )), "#", "%23");


/* ==================== */
/* == Timing functions
 *
 * @note Simple cubic-bezier curves for timing functions
 * @author David K. Piano
 * @link https://twitter.com/DavidKPiano/status/965704092545900544
/* ==================== */

$timing-functions: (
  enter: cubic-bezier(0, .5, .5, 1),
  exit: cubic-bezier(.5, 0, 1, .5),
  move: cubic-bezier(.5, 0, .5, 1)
);

/* ==================== */
/* == Spacings
/* ==================== */

$full-width: false !default;
$base: 1140 / 16 !default;
$width: em( 980 ) !default;
$gutter: rem( 16 ) !default;


