/* ==================== */
/* == Formage
/* ==================== */

$form-is-normalize: true;
$form-is-box-sizing: true;
$form-is-visually-hidden: true;

$form-input-padding: em( 8 );
$form-input-width: 400;
$form-input-background: palette( default, secondary );

$form-border-radius: em( 4 );
$form-border-color: palette( dominant, dark );
$form-border-shadow: palette( dominant, dark );

$form-button-background: palette( dominant, dark );
$form-button-border: palette( dominant, dark );
$form-button-color: palette( dominant, contrast );

$form-screen-small: 768;

$form-placeholder: palette( muted, light );

$form-help: palette( default );
$form-error: palette( alert, dark );
$form-valid: palette( success );
